"use client";

import { Sheet, SheetContent, SheetHeader, SheetTitle } from "@/components/ui/sheet";
import { Button } from "@/components/ui/button";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";

interface MessagePanelProps {
  isOpen: boolean;
  onClose: () => void;
}

interface Conversation {
  id: number;
  name: string;
  lastMessage: string;
  timestamp: string;
  unread: boolean;
}

interface Group {
  id: number;
  name: string;
  members: number;
  lastActivity: string;
}

const dummyConversations: Conversation[] = [
  {
    id: 1,
    name: "Sarah Chen",
    lastMessage: "Hey! How's the project going?",
    timestamp: "2m ago",
    unread: true
  },
  {
    id: 2,
    name: "Mike Rodriguez",
    lastMessage: "Thanks for the help earlier!",
    timestamp: "15m ago",
    unread: false
  },
  {
    id: 3,
    name: "Emma Thompson",
    lastMessage: "Let's catch up soon",
    timestamp: "1h ago",
    unread: true
  },
  {
    id: 4,
    name: "David Park",
    lastMessage: "The meeting is at 3 PM",
    timestamp: "2h ago",
    unread: false
  }
];

const dummyGroups: Group[] = [
  {
    id: 1,
    name: "Design Team",
    members: 8,
    lastActivity: "5m ago"
  },
  {
    id: 2,
    name: "Project Alpha",
    members: 12,
    lastActivity: "30m ago"
  },
  {
    id: 3,
    name: "Coffee Chat",
    members: 25,
    lastActivity: "1h ago"
  }
];

export default function MessagePanel({ isOpen, onClose }: MessagePanelProps) {
  return (
    <Sheet open={isOpen} onOpenChange={onClose}>
      <SheetContent side="left" className="w-80 bg-gradient-to-b from-purple-50 to-sky-50">
        <SheetHeader className="border-b border-purple-200 pb-4">
          <SheetTitle className="text-purple-700 text-xl">Messages</SheetTitle>
        </SheetHeader>
        
        <div className="flex flex-col h-full pt-4 space-y-6">
          {/* Direct Messages Section */}
          <div>
            <h3 className="text-sm font-semibold text-purple-600 mb-3 px-2">Direct Messages</h3>
            <div className="space-y-2">
              {dummyConversations.length === 0 ? (
                <p className="text-gray-500 text-sm px-2">No conversations</p>
              ) : (
                dummyConversations.map((conversation) => (
                  <Card key={conversation.id} className="cursor-pointer hover:bg-white/70 transition-colors border-purple-100">
                    <CardContent className="p-3">
                      <div className="flex items-center space-x-3">
                        <div className="w-8 h-8 bg-gradient-to-br from-purple-400 to-sky-300 rounded-full flex items-center justify-center">
                          <span className="text-white font-semibold text-xs">
                            {conversation.name.split(' ').map(n => n[0]).join('')}
                          </span>
                        </div>
                        <div className="flex-1 min-w-0">
                          <div className="flex items-center justify-between">
                            <p className="text-sm font-medium text-gray-900 truncate">
                              {conversation.name}
                            </p>
                            <span className="text-xs text-gray-500">{conversation.timestamp}</span>
                          </div>
                          <p className="text-xs text-gray-600 truncate">
                            {conversation.lastMessage}
                          </p>
                        </div>
                        {conversation.unread && (
                          <div className="w-2 h-2 bg-sky-400 rounded-full"></div>
                        )}
                      </div>
                    </CardContent>
                  </Card>
                ))
              )}
            </div>
          </div>

          {/* Groups Section */}
          <div>
            <h3 className="text-sm font-semibold text-purple-600 mb-3 px-2">Groups</h3>
            <div className="space-y-2">
              {dummyGroups.length === 0 ? (
                <p className="text-gray-500 text-sm px-2">No groups</p>
              ) : (
                dummyGroups.map((group) => (
                  <Card key={group.id} className="cursor-pointer hover:bg-white/70 transition-colors border-purple-100">
                    <CardContent className="p-3">
                      <div className="flex items-center space-x-3">
                        <div className="w-8 h-8 bg-gradient-to-br from-sky-400 to-purple-400 rounded-full flex items-center justify-center">
                          <span className="text-white font-semibold text-xs">
                            {group.name.split(' ').map(n => n[0]).join('')}
                          </span>
                        </div>
                        <div className="flex-1 min-w-0">
                          <div className="flex items-center justify-between">
                            <p className="text-sm font-medium text-gray-900 truncate">
                              {group.name}
                            </p>
                            <span className="text-xs text-gray-500">{group.lastActivity}</span>
                          </div>
                          <p className="text-xs text-gray-600">
                            {group.members} members
                          </p>
                        </div>
                      </div>
                    </CardContent>
                  </Card>
                ))
              )}
            </div>
          </div>

          {/* Voice/Video Calls Section */}
          <div>
            <h3 className="text-sm font-semibold text-purple-600 mb-3 px-2">Quick Actions</h3>
            <div className="space-y-2">
              <Button 
                variant="outline" 
                className="w-full justify-start border-purple-200 text-purple-600 hover:bg-purple-50"
              >
                Start Voice Call
              </Button>
              <Button 
                variant="outline" 
                className="w-full justify-start border-sky-200 text-sky-600 hover:bg-sky-50"
              >
                Start Video Call
              </Button>
              <Button 
                variant="outline" 
                className="w-full justify-start border-purple-200 text-purple-600 hover:bg-purple-50"
              >
                Create Group
              </Button>
            </div>
          </div>

          {/* Message Input */}
          <div className="mt-auto pt-4 border-t border-purple-200">
            <div className="flex space-x-2">
              <input
                type="text"
                placeholder="Type a message..."
                className="flex-1 px-3 py-2 text-sm border border-purple-200 rounded-md focus:outline-none focus:ring-2 focus:ring-purple-400 focus:border-transparent"
              />
              <Button 
                size="sm"
                className="bg-purple-600 hover:bg-purple-700 text-white"
              >
                Send
              </Button>
            </div>
          </div>
        </div>
      </SheetContent>
    </Sheet>
  );
}
