"use client";

import { useState } from "react";
import Header from "@/components/Header";
import Feed from "@/components/Feed";
import VerticalVideoSection from "@/components/VerticalVideoSection";
import VideoHub from "@/components/VideoHub";
import MessagePanel from "@/components/MessagePanel";

export default function Home() {
  const [activeTab, setActiveTab] = useState<"feed" | "vertical" | "hub">("feed");
  const [isMessagePanelOpen, setIsMessagePanelOpen] = useState(false);

  const renderContent = () => {
    switch (activeTab) {
      case "feed":
        return <Feed />;
      case "vertical":
        return <VerticalVideoSection />;
      case "hub":
        return <VideoHub />;
      default:
        return <div className="p-8 text-center text-gray-500">Content not available</div>;
    }
  };

  return (
    <div className="min-h-screen bg-white">
      <Header 
        activeTab={activeTab}
        setActiveTab={setActiveTab}
        onToggleMessagePanel={() => setIsMessagePanelOpen(!isMessagePanelOpen)}
      />
      
      <main className="pt-20">
        {renderContent()}
      </main>

      <MessagePanel 
        isOpen={isMessagePanelOpen}
        onClose={() => setIsMessagePanelOpen(false)}
      />
    </div>
  );
}
